export default{
  data() {
    return {
      title: '',
      type: '',
      showInfo: false,
      code_word: '获取验证码',
      mobile: '',
      password: '',
      password2: '',
      code: '',
      btnValue: '',
      ishide: false
    }
  },
  onLoad(option) {
    that.type = option.type
    this.mobile = option.mobile
    console.log('password接收到的参数', option)
    if(option.type === 'register') {
      this.title = '设置密码'
      this.btnValue = '注册'
      return
    }
    if(option.type === 'forget') {
      this.title = '重置密码'   
      this.btnValue = '保存并登录'
      return
    }
  },
  computed: {
    windowHeight() {
      return this.$store.state.windowHeight
    },
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    sureAction() {
      let that = this
      console.log('fjafjf')
      if(!that.password) {
        uni.showToast({
          title: '密码不能为空',
          icon: 'none'
        })
        return
      }
      if(that.password !== that.password2) {
        uni.showToast({
          title: '两次密码输入不一致',
          icon: 'none'
        })
        return
      }
      
      switch(that.type) {
        case 'forget':
          that.$http({
            url: that.$api.resetpassword,
            method: 'POST',
            data: {
              mobile: that.mobile,
              newpassword: that.password
            },
            cb: (err, res) => {
              if(!err && res.code === 1) {
                that.$store.commit('login', {
                  mobile: res.data.mobile,
                  token: res.data.token
                })
                uni.showToast({
                  title: '重置密码成功即将自动登录',
                  icon: 'none'
                })
                setTimeout(function() {
                  uni.switchTab({
                    url: '../index/index'
                  })  
                }, 1000)
                
              } else if(res.code === 0 && res.msg) {
                uni.showToast({
                  title: res.msg,
                  icon: 'none'
                })
                return
              } else {
                uni.showToast({
                  title: '重置密码失败',
                  icon: 'none'
                })
                return
              }
            }
          })
          break
        case 'register':
          that.$http({
            url: that.$api.register,
            method: 'POST',
            data: {
              mobile: that.mobile,
              password: that.password
            },
            cb: (err, res) => {
              if(!err && res.code === 1) {
                uni.showToast({
                  title: '注册成功',
                  icon: 'none'
                })
                uni.switchTab({
                  url: './login'
                })
              } else if(res.code === 0) {
                uni.showToast({
                  title: res.msg,
                  icon: 'none'
                })
              } else {
                uni.showToast({
                  title: '注册失败',
                  icon: 'none'
                })
              }
            }
          })
          break
      }
    }
  }
}
